.bb-button-block + .bb-button-block {
	@apply mt-2;
}

// Streamlined styles for the button block. These *only* apply to the button block, not the other UI button-like elements!
.bb-button {
	@apply gap-2 no-underline font-sans font-robotonormal border;
	@apply rounded-md inline-flex flex-shrink-0 cursor-pointer select-none flex-wrap items-center justify-start border-transparent text-left transition duration-200 ease-out text-xl leading-none;
	@apply px-2.5 py-1.5;

	/* disabled */
	&-disabled,
	&[disabled],
	&:disabled {
		@apply pointer-events-none;
	}

	// Default dimensions of SVG icon
	svg {
		@apply h-8 w-8;
	}

	// Button sizes
	&.btn-xs {
		@apply px-1.5 py-0.5 text-sm;
		svg {
			@apply icon-xs;
		}
	}
	&.btn-sm {
		@apply px-1.5 py-0.5 text-base lg:px-2 lg:py-0.5 lg:text-base;
		svg {
			@apply icon-sm;
		}
	}
	&.btn-lg {
		@apply px-2 py-1 text-xl lg:px-2.5 lg:py-1 lg:text-2xl;
		svg {
			@apply icon-lg;
		}
	}
	&.btn-xl {
		@apply px-2 py-1 text-2xl lg:px-3 lg:py-1.5 lg:text-3xl;
		svg {
			@apply icon-lg;
		}
	}

	animation: button-pop var(--animation-btn, 0.25s) ease-out;

	&:active:hover,
	&:active:focus {
		animation: bb-button-pop 0s ease-out;
		transform: scale(var(--btn-600-scale, 0.98));
	}

	// Button styles
	// Note: only use default, light, and dark variant of colors for all styling!
	@each $color in $bb_colors {
		// Default
		&-#{$color} {
			@apply border-#{$color} bg-#{$color} text-white outline-#{$color} hover:border-#{$color}-dark hover:bg-#{$color}-dark;
		}
		// Outline
		&-#{$color}.bb-button-outline {
			@apply text-#{$color} border-current bg-transparent shadow-none hover:border-#{$color}-dark hover:bg-#{$color}-dark hover:text-white;
		}
		// Ghost
		&-#{$color}.bb-button-ghost {
			@apply border border-transparent bg-transparent text-current shadow-none outline-current hover:bg-#{$color}-light hover:border-opacity-0 hover:bg-opacity-20;
		}
	}
}

@keyframes bb-button-pop {
	0% {
		transform: scale(var(--btn-600-scale, 0.98));
	}

	40% {
		transform: scale(1.02);
	}

	100% {
		transform: scale(1);
	}
}
